<template>
  <!-- d2-container  https://doc.d2admin.fairyever.com/zh/sys-components/container.html -->
  <d2-container class="page-GatewayDetail">
    <div style="height:40%;width:90%">
      <el-form label-position="left" inline class="demo-table-expand">
        <el-form-item label="">
          <span>{{ detail.code }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ detail.name }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ detail.systemId }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ detail.status }}</span>
        </el-form-item>
        <el-form-item label="">
          <span>{{ detail.description }}</span>
        </el-form-item>
      </el-form>
    </div>
    <hr/>
    <route-list :gateway-id="gatewayId"></route-list>
    <template slot="header"></template>
    <template slot="footer"> 1 </template>
  </d2-container>
</template>

<script>
  // 
  import _ from 'lodash';
  import {mapActions} from 'vuex'
  import RouteList from '../../route/components/D2Crud/D2Crud';

  // 
  export default {
    name: 'gateway-detail',
    components: {RouteList},
    computed: {
      detail() {
        return _.cloneDeep(this.$store.state.gateway.one);
      },
    },
    // 
    data () {
      return {
        gatewayId: '',
      }
    },
    // 
    watch: {
    },
    // 
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted () {
      this.gatewayId = this.$route.params.gatewayId;
      this.refresh();
    },
    beforeUpdate () {},
    updated () {},
    activated () {},
    deactivated () {},
    beforeDestroy () {},
    destroyed () {},
    // 
    methods: {
      ...mapActions({
        queryGatewayById: 'queryGatewayByIdAction',
      }),
      refresh() {
        this.queryGatewayById(this.gatewayId);
      },
    }
  }
</script>