<template>
  <div class="d2-crud">
    <!--  -->
    <!--  https://d2-projects.github.io/d2-admin-doc/zh/ecosystem-d2-crud/ -->
    <!--  https://d2-projects.github.io/d2-admin/#/demo/d2-crud/index -->
    <d2-crud
      ref="d2Crud"
      index-row
      :columns="columns"
      :options="options"
      :data="data"
      :loading="loading"
      :pagination="pagination"
      title=""
      add-title=""
      :add-template="addTemplate"
      :form-options="formOptions"
      @row-add="handleRowAdd"
      :rowHandle="rowHandle"
      @row-remove="handleRowRemove"
      @dialog-cancel="handleDialogCancel"
      @row-click="handleRowClick"
      @pagination-current-change="paginationCurrentChange">
      <el-button icon="el-icon-plus" slot="header" size="middle"
                 style="margin-bottom: 5px" @click="addRow"></el-button>
    </d2-crud>
  </div>
</template>

<script>
//import Vue from 'vue'
import D2Crud from '@d2-projects/d2-crud'
import {mapActions} from 'vuex'
import RouteApi from '@/api/route'
import _ from 'lodash';
import FilterList from '../filter-list';

//Vue.use(D2Crud)

export default {
  name:'route-table',
  components:{D2Crud,FilterList},
  computed: {
    data() {
      return _.cloneDeep(this.$store.state.route.res.records)
    },
    pagination() {
      return _.cloneDeep(this.$store.state.route.res.page)
    }
  },
  props: {
    gatewayId: '',
  },
  data() {
    return {
      detailData : {},
      columns: [
        {
          title: '',
          key: 'name'
        },
        {
          title: '',
          key: 'code',
        },
        {
          title: '',
          key: 'bindType',
        },
        {
          title: '',
          key: 'createTime'
        },
        {
          title: '',
          key: 'updateTime'
        },
        {
          title: '',
          key: 'gatewayId',
        },
        {
          title: '',
          key: 'active',
        },
      ],
      addButton: {
        icon: 'el-icon-plus',
        size: 'small'
      },
      rowHandle: {
        columnHeader: '',
        remove: {
          icon: 'el-icon-delete',
          size: 'small',
          fixed: 'right',
          confirm: true,
          show (index, row) {
            return true;
          },
          disabled (index, row) {
            return false;
          }
        }
      },
      addTemplate: {
        name: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        code: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        description: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        gatewayId: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        bindId: {
          title: 'id',
          value: '',
          component: {
            span: 12
          }
        },
        filters: {
          title: '',
          value: {text:'', list:[{value:''}]},
          component: {
            name: FilterList,
          }
        },
        predicates: {
          title: '',
          value: {text:'', list:[{value:''}]},
          component: {
            name: FilterList,
          }
        },
        priority: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        }
      },
      editTemplate: {
        name: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        code: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        description: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        uri: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        filters: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        predicates: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        priority: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        }
      },
      formRules: {
        name: [ { required: true, message: '', trigger: 'blur' } ],
        code: [ { required: true, message: '', trigger: 'blur' } ],
        gatewayId: [ { required: true, message: '', trigger: 'blur' } ],
        description: [ { required: false} ]
      },
      formOptions: {
        labelWidth: '80px',
        labelPosition: 'left',
        saveLoading: false,
        gutter: 20
      },
      loading: false,
      page: {
        currentPage: 1,
        pageSize: 10,
      },
      options: {
        stripe: true
      }

    }
  },
  watch: {
    gatewayId: function (val) {
      this.queryRoutePage({...this.page, gatewayId: val});
    }
  },
  mounted() {
   /* this.queryRoutePage({
      id: "",
      code: "",
      name: "",
      status: "",
      startCreateTime: "",
      endCreateTime: "",
      createTime: "",
      description: "",
      rsskey: "",
      userId: "",
      userName: "",
    });*/
    if (_.isEmpty(this.gatewayId)) {
      this.queryRoutePage({...this.page, gatewayId: this.gatewayId});
    }
    this.fetchData();
  },
  methods: {
    addRow () {
      this.$refs.d2Crud.showDialog({
        mode: 'add'
      })
    },
    paginationCurrentChange (currentPage) {
      this.page.currentPage = currentPage
      this.fetchData()
    },
    fetchData () {
      this.loading = true;
      this.queryRoutePage({...this.page});
      this.loading = false;
    },
    handleRowAdd (row, done) {
      this.formOptions.saveLoading = true;
      row.filters = row.filters.list.map(item => item.value);
      row.predicates = row.predicates.list.map(item => item.value);
      RouteApi.save(row).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
      this.queryRoutePage({...this.page});
      }).catch();
      done();
      this.formOptions.saveLoading = false
    },
    handleRowEdit ({index, row}, done) {
      this.formOptions.saveLoading = true
      RouteApi.update(row).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
      this.queryRoutePage({...this.page});
      });
      done()
      this.formOptions.saveLoading = false
    },
    handleRowRemove ({index, row}, done) {
      RouteApi.deletes({"ids": row.id}).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
      this.queryRoutePage({...this.page});
      });
      done()
    },
    handleDialogCancel (done) {
      this.$message({
        message: '',
        type: 'warning'
      });
      done()
      this.queryRoutePage({...this.page});
    },
    handleSelectionChange (selection) {
      console.log(selection)
    },
    ...mapActions({
      queryRoutePage: 'queryRoutePageAction', //  `this.add()`  `this.$store.dispatch('increment')`
      saveRoute: 'saveRouteAction',
      deleteRoute: 'deleteRouteAction',
      updateRoute: 'updateRouteAction',
    }),
    cloneDeep(val){
      return _.cloneDeep(val);
    },
    handleRowClick(row, column, event) {
      if (column.property === 'name') {
        this.$router.push({
          name: 'route-detail',
          params: {routeId: row.id},
        });
      }
    },
  }
}
</script>
