<template>
  <div class="d2-crud">
    <!--  -->
    <!--  https://d2-projects.github.io/d2-admin-doc/zh/ecostrategyIp-d2-crud/ -->
    <!--  https://d2-projects.github.io/d2-admin/#/demo/d2-crud/index -->
    <d2-crud
      ref="d2Crud"
      index-row
      :columns="columns"
      :options="options"
      :data="data"
      :loading="loading"
      :pagination="pagination"
      title="IP"
      add-title="IP"
      :add-template="addTemplate"
      :form-options="formOptions"
      @row-add="handleRowAdd"
      edit-title="IP"
      :edit-template="editTemplate"
      @row-edit="handleRowEdit"
      :rowHandle="rowHandle"
      @row-remove="handleRowRemove"
      @dialog-cancel="handleDialogCancel"
      @pagination-current-change="paginationCurrentChange">
      <el-button icon="el-icon-plus" slot="header" size="middle"
                 style="margin-bottom: 5px" @click="addRow"></el-button>
    </d2-crud>
  </div>
</template>

<script>
//import Vue from 'vue'
import D2Crud from '@d2-projects/d2-crud'
import {mapActions} from 'vuex'
import StrategyIpApi from '@/api/strategyIp'
import util from '@/libs/util.js'
import _ from 'lodash'

//Vue.use(D2Crud)

export default {
  name:'strategyIp-table',
  components:{D2Crud},
  computed: {
    data() {
      return _.cloneDeep(this.$store.state.strategyIp.res.records)
    },
    pagination() {
      return _.cloneDeep(this.$store.state.strategyIp.res.page)
    }
  },
  data() {
    return {
      detailData : {},
      columns: [
        {
          title: '',
          key: 'name',
        },
        {
          title: '',
          key: 'type'
        },
        {
          title: '',
          key: 'gatewayId',
        },
        {
          title: '',
          key: 'description',
        },
        {
          title: 'IP',
          key: 'ipList',
        }
      ],
      addButton: {
        icon: 'el-icon-plus',
        size: 'small'
      },
      rowHandle: {
        columnHeader: '',
        edit: {
          icon: 'el-icon-edit',
          text: '',
          size: 'small',
          show (index, row) {
            return true;
          },
          disabled (index, row) {
            return false;
          }
        },
        remove: {
          icon: 'el-icon-delete',
          size: 'small',
          fixed: 'right',
          confirm: true,
          show (index, row) {
            return true;
          },
          disabled (index, row) {
            return false;
          }
        }
      },
      addTemplate: {
        name: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        type: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        gatewayId: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        description: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        ipList: {
          title: 'IP',
          value: '',
          component: {
            text: 24
          }
        },
      },
      editTemplate: {
        name: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        type: {
          title: '',
          value: '',
          component: {
            span: 12
          }
        },
        description: {
          title: '',
          value: '',
          component: {
            span: 24
          }
        },
        ipList: {
          title: 'IP',
          value: '',
          component: {
            text: 24
          }
        },
      },
      formRules: {
        name: [{required: true, message: 'IP', trigger: 'blur'}],
        type: [{required: true, message: 'IP', trigger: 'blur'}],
        ipList: [{required: true, message: 'IP', trigger: 'blur'}],
        description: [{required: false}]
      },
      formOptions: {
        labelWidth: '80px',
        labelPosition: 'left',
        saveLoading: false,
        gutter: 20
      },
      loading: false,
      page: {
        currentPage: 1,
        pageSize: 10,
      },
      options: {
        stripe: true
      }

    }
  },
  watch: {
   /* handleRowAdd: function () {
      this.queryStrategyIpPage({});
    }*/
  },
  mounted() {
   /* this.queryStrategyIpPage({
      id: "",
      code: "",
      name: "",
      status: "",
      startCreateTime: "",
      endCreateTime: "",
      createTime: "",
      description: "",
      rsskey: "",
      userId: "",
      userName: "",
    });*/
    this.fetchData();
  },
  methods: {
    addRow () {
      this.$refs.d2Crud.showDialog({
        mode: 'add'
      })
    },
    paginationCurrentChange (currentPage) {
      this.page.currentPage = currentPage
      this.fetchData()
    },
    fetchData () {
      this.loading = true;
      this.queryStrategyIpPage({...this.page});
      this.loading = false;
    },
    handleRowAdd (row, done) {
      this.formOptions.saveLoading = true
      StrategyIpApi.save(row).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
      this.queryStrategyIpPage({...this.page});
      }).catch();
      done();
      this.formOptions.saveLoading = false
    },
    handleRowEdit ({index, row}, done) {
      this.formOptions.saveLoading = true
      StrategyIpApi.update(row).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
      this.queryStrategyIpPage({...this.page});
      });
      done()
      this.formOptions.saveLoading = false
    },
    handleRowRemove ({index, row}, done) {
      StrategyIpApi.deletes({"ids": row.id}).then(() => {
        this.$message({
          message: '',
          type: 'success'
        })
        this.queryStrategyIpPage({...this.page});
      });
      done()
    },
    handleDialogCancel (done) {
      this.$message({
        message: '',
        type: 'warning'
      });
      done()
      this.queryStrategyIpPage({...this.page});
    },
    handleSelectionChange (selection) {
      console.log(selection)
    },
    ...mapActions({
      queryStrategyIpPage: 'queryStrategyIpPageAction', //  `this.add()`  `this.$store.dispatch('increment')`
      saveStrategyIp: 'saveStrategyIpAction',
      deleteStrategyIp: 'deleteStrategyIpAction',
      updateStrategyIp: 'updateStrategyIpAction',
    }),
    cloneDeep(val){
      return _.cloneDeep(val);
    },
  }
}
</script>
