<template>
  <el-form ref="form" v-model="thisQuery">
    <el-row :gutter="20" type="flex">
      <el-col>
        <el-input placeholder="" v-model="thisQuery.name"></el-input>
      </el-col>
      <el-col>
        <el-input placeholder="" v-model="thisQuery.code"></el-input>
      </el-col>
      <el-col>
        <el-select v-model="thisQuery.systemId" placeholder="" clearable>
          <el-option
            v-for="item in systemList"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-col>
      <el-col>
        <el-button class="margin-left-20" @click="search"></el-button>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
  import {mapActions} from 'vuex';
  import _ from 'lodash';
  export default {
    name: 'application-search',
    components: {
    },
    props: {
      query:{

      }
    },
    // 
    data() {
      return {
        thisQuery:{},
      }
    },
    // 
    computed: {
      systemList() {
        return this.$store.state.systemInfo.list
      },
    },
    // 
    watch: {

    },
    // 
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted() {
      this.thisQuery = _.cloneDeep(this.query);
      this.querySystemInfoSelectList({});
    },
    beforeUpdate () {},
    updated () {},
    activated () {},
    deactivated () {},
    beforeDestroy () {},
    destroyed () {},
    // 
    methods: {
      ...mapActions({
        querySystemInfoSelectList: 'querySystemInfoSelectListAction', //  `this.add()`  `this.$store.dispatch('increment')`
      }),
      search() {
        this.query.systemId = this.thisQuery.systemId;
        this.query.code = this.thisQuery.code;
        this.query.name = this.thisQuery.name;
        this.$emit('queryApplication');
      }
    }
  }
</script>

